.btn {
  border: none;
  background-color: #6953f0;
  border-radius: 6px;
  align-items: center;
  color: #fff;
  cursor: pointer;
  display: inline-flex;
  font-family: 'Inter', 'Arial', 'Helvetica', sans-serif;
  font-size: 1.2rem;
  font-weight: 700;
  justify-content: center;
  line-height: 1;
  letter-spacing: 1px;
  max-height: 6.4rem;
  overflow: hidden;
  padding: 1rem 1.2rem;
  position: relative;
  text-transform: uppercase;
  vertical-align: bottom;
}
.btn:before {
  background-color: #573eee;
  content: '';
  left: 0;
  height: 100%;
  position: absolute;
  top: 0;
  transition: all 0.4s cubic-bezier(0.4, 0, 0, 1);
  width: 0;
  z-index: 0;
}
.btn__text {
  position: relative;
  transition: all 0.4s cubic-bezier(0.4, 0, 0, 1);
  z-index: 10;
}
.btn__text:nth-child(2) {
  opacity: 0;
  position: absolute;
  transform: translateY(100%);
}
.btn_ghost {
  background-color: transparent;
}
.btn_ghost:before {
  display: none;
}
.btn_ghost .btn__text {
  color: #6953f0;
}
.btn:hover:before {
  width: 100%;
}
.btn:hover .btn__text:nth-child(1) {
  opacity: 0;
  transform: translateY(-100%);
}
.btn:hover .btn__text:nth-child(2) {
  opacity: 1;
  transform: none;
}
.btn--small {
  padding: 1.6rem 2.4rem;
}
.btn--transparent {
  background-color: rgba(255,255,255,0.2);
}
.btn--full {
  width: 100%;
}
.btn + .btn {
  margin-left: 2rem;
}
.btn + .slider {
  margin-top: 3.2rem;
}
.form-control {
  margin-bottom: 1.6rem;
  position: relative;
  width: 100%;
