.checkbox {
  align-items: center;
  cursor: pointer;
  display: flex;
  margin-bottom: 1em;
  user-select: none;
}
.checkbox__label {
  background: transparent;
  border-radius: 8px;
  border: 1px solid #dee6eb;
  cursor: pointer;
  height: 1.8rem;
  width: 1.8rem;
  display: inline-block;
  flex-shrink: 0;
  margin-right: 0.4rem;
  position: relative;
  transform: translateY(-1px);
  z-index: 1;
}
.checkbox__label:after {
  color: #18191b;
  content: url("../../img/check.svg");
  display: none;
  font-size: 1.3rem;
  left: 2px;
  position: absolute;
  top: 2px;
  z-index: 10;
}
.checkbox__text {
  display: inline-block;
}
.checkbox .link {
  display: inline-block;
  margin-bottom: 0;
}
.checkbox input {
  display: none;
}
.checkbox input:checked + .checkbox__label:after {
  display: block;
}
.radio {
  align-items: center;
  cursor: pointer;
  display: flex;
  margin-bottom: 1rem;
  user-select: none;
}
.radio__label {
  background: #fff;
  border-radius: 50%;
