  height: 3.2rem;
  width: 3.2rem;
  display: inline-block;
  margin-right: 1rem;
  position: relative;
  z-index: 1;
}
.radio__label:after {
  align-items: center;
  content: url("../../img/check.svg");
  display: none;
  justify-content: center;
  margin-top: -2px;
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 10;
}
.radio input {
  display: none;
}
.radio input:checked + .radio__label {
  border-color: #6953f0;
}
.radio input:checked + .radio__label:after {
  display: flex;
}
